/* Navbar component is included in App.js, therefore, we have access to the style.css variables */
.custom-nav {
    display: flex;
    justify-content: space-around;
    align-items: center;
}

#custom-dropdown {
    background-color: transparent;
    border: none;
    color: var(--light-pink);
    font-size: 150%;
}

#custom-menu {
    background-color: var(--trans-shade4);
}

#custom-menu button {
    background-color: transparent;
    border: none;
}

.dropdown-item:hover {
    /* !important to override bootstrap instead of creating an id for each dropdown item */
    background-color: transparent !important;
}

.header-bg {
    display: flex;
    background-color: var(--trans-shade4);
}

.nav-font {
    text-decoration: none;
    /* font-family: var(--rubik); */
    color: var(--light-pink);
    font-size: 150%;
    background-color: transparent;
    border: none;
    margin: 0;
    padding: 0;
}

.nav-font:hover {
    color: var(--light-pink);
    cursor: pointer;
}

.nav-img {
    max-width: 25%;
}

/* media queries */

/* ipad air */
@media screen and (max-width: 820px) {
   .nav-font, #custom-dropdown {
    font-size: 120%;
   } 
   .nav-img {
    max-width: 35%;
   }
}

/* Surface Duo */
@media screen and (max-width: 540px) {
    .header-bg {
        flex-flow: column wrap;
        align-items: center;
    }
    .nav-img {
        max-width: 50%;
    }

    @media screen and (max-width: 500px) {
        .nav-font, #custom-dropdown {
            font-size: 100%;
        }
    }
}

@media screen and (max-width: 360px) {
    .nav-font, #custom-dropdown {
        font-size: 80%;
    }
}

/* Galaxy Fold */
@media screen and (max-width: 280px) {
       .nav-img {
        max-width: 75%;
    }
}
/* !important on override of bootstrap */
.custom-card {
    width: 70%;
    /* font-family: var(--rubik); */
    border-radius: 1rem;
    margin: 3%;
}

.about-me-body {
    min-height: 90vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.custom-card-header {
    background-color: var(--light-blue);
    color: var(--shade4);
    margin-bottom: 0;
    padding: 1%;
    border-top-left-radius: 1rem;
    border-top-right-radius: 1rem;
}

.custom-card-body {
    background-color: var(--shade4);
    padding: 1%;
    border-bottom-left-radius: 1rem;
    border-bottom-right-radius: 1rem;

}

.card-title {
    color: var(--matte-blue);
}

.card-text {
    color: var(--shade1);
}

.name-container {
    display: flex;
    align-items: center;
}

.avatar {
    width: 15%;
    border-radius: 100%;
