}
/* media queries */
/* Surface Duo */
@media screen and (max-width: 540px) {
    
    .custom-card {
        width: 85%;
    }

}

/* Samsung Galaxy S20 Ultra */
@media screen and (max-width: 412px) {
    
    .custom-card {
        width: 100%;
    }

}

/* Galaxy Fold */
@media screen and (max-width: 280px) {
    .card-title {
        font-size: 100%;
    }

    .custom-card-header {
        font-size: 120%;
    }

    .card-text {
        font-size: 90%;
    }
}
.portfolio-body {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around;
    align-items: center;
    min-height: 80vh;
}

.custom-figure {
    margin: 3%;
    display: flex;
    width: 650px;
    height: 325px;
    border: 2px solid var(--light-pink);
    border-radius: 5px;
    position: relative;
}

.custom-figure-img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 3px;
}

.custom-figure-content {
    display: flex;
    flex-flow: column wrap;
    justify-content: center;
    align-items: center;
    position: absolute;
    width: 100%;
    height: 100%;
    color: transparent;
}

.custom-figure-content:hover {
    color: var(--shade4);
    background-color: var(--trans-shade2);

}

.project-title {
    font-family: var(--distressed);
}

.project-content {
    /* font-family: var(--rubik); */
    text-align: center;
}

/* target the span element's text color in the project component */
.custom-figure-content:hover .alt-color {
    color: var(--light-pink);
}

.project-icon-container {
    font-size: 4.5rem;
}

.project-icon-container a {
    margin-left: 5px;
    margin-right: 5px;
    /* unset the <a> tag link styling for the color so it will inherit the transparency until the entire div is shown on hover */
    color: unset;
}

.project-icon-container a:hover {
    cursor: pointer;
    color: var(--light-pink);
}

/* Surface Duo */
@media screen and (max-width: 540px) {

    .custom-figure {
        width: 400px;
        height: 200px;
    }
    .project-title {
        font-size: 130%;
    }
    
    .project-content {
        font-size: 80%;
    }

    .project-icon-container {
        font-size: 1.5rem;
    }
}

